import { Command } from '@ali/4ever-cangjie';
import insertRefer from '../commands/insertRefer';
import createRenderNode from '../createRenderNode';
import * as actions from '../actions';
import onAction from '../handlers/onAction';
import { BiPluginConfig, BiPlugin } from '../../../types';
import { moFactory } from '@ali/4ever-plugin-refer';

export { actions };

const commands: Record<string, Command> = {
  insertRefer,
};

function createReferPlugin(config: BiPluginConfig): BiPlugin {
  const referConfig = config.refer || {};

  return {
    ...moFactory({}),
    commands,
    renderNode: createRenderNode(referConfig),
    onAction,
    models: {
      klass: 'inline',
      type: 'refer',
    },
  };
}

createReferPlugin.actions = actions;
createReferPlugin.commands = commands;

export default createReferPlugin;
